<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="12%">年级</th>
          <th width="12%">班级</th>
          <th width="12%">班主任</th>
          <th width="12%">家访时间</th>
          <th width="12%">学生</th>
          <th width="12%">家访对象</th>
          <th width="12%">家访内容</th>
          <th width="12%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>{{item.gradeName}}</td>
          <td>{{item.className}}</td>
          <td>{{item.teacherName}}</td>
          <td>{{item.visitDate | substrTime }}</td>
          <td>{{item.studentName}}</td>
          <td>{{item.visitObject}}</td>
          <td>{{item.visitContent}}</td>
          <td>
            <span class="oper"
                  @click="$router.push(`/student-work/home_visit/detail?id=`+item.id+``)">详情</span>
          </td>
        </tr>
        </tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        nav: [{name: '家访管理', path: ''}],
        searchData: [
          {type: 'select', title: '年级', model: 'gradeId', option: []},
          {
            type: 'dateSection', title: '时间',
            startDate: new Date().setTime(new Date().getTime() - 3600 * 1000 * 24 * 90), endDate: new Date(),
            modelName: ['startDate', 'endDate']
          },{type: 'input', placeholder: '标题'},],
        table: {
          url: '/homevisiting/queryHomeVisitingPageList.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            gradeId: null,
            keywords: null,
            startDate: new Date(new Date().setMonth(new Date().getMonth() - 6)).$Format('yyyy-MM-dd' + ' 00:00:00'),
            endDate: new Date().$Format('yyyy-MM-dd hh:mm:ss')
          }
        }
      }
    },
    methods: {
      // 获取年级
      async onGetGradeList () {
        let arr = await this.$ajax({
          url: '/grade/queryGradeList.do'
        })
        arr = arr.map(item => {
          return {
            value: item.id,
            label: item.name
          }
        })
        this.searchData[0].option = [{label: '全部', value: null}].concat(arr)
      }
    },
    async mounted () {
      await this.onGetGradeList()
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
</style>
